@import url(https://fonts.googleapis.com/css2?family=Heebo&display=swap);
*,
*::before,
*::after {
  box-sizing: border-box;
}

body,
h1,
h2,
h3,
h4,
p {
  margin: 0;
  padding: 0;
}

img {
  max-width: 100%;
  display: block;
}

ul,
ol {
  list-style: none;
}

a {
  text-decoration: none;
}

body {
  margin-top: 30px;
  font-family: 'Heebo', sans-serif;
}

.App {
