}

.wrapper {
  background-image: url(/goit-react-hw-02-phonebook/static/media/PinClipart.com_phone-images-clip-art_360232.6e4b35f8.png);
  background-size: cover;
  background-repeat: no-repeat;
  height: 680px;
  width: 315px;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
  padding: 50px 20px 40px 20px;
  overflow-y: auto;
}

.wrapper::-webkit-scrollbar {
  width: 0px;
}

